.App {
    margin: 0 auto;
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: 300px 1fr 255px;
    height: 100vh;
    width: 100%;
    text-align: center;
}

.header {
    grid-column: 1/-1;
    grid-row: 1/2;
}

.imageGrid {
    grid-column: 1/-1;
    grid-row: 3/4;
    width: auto;
}

/* desktop */
@media (min-width: 1025px) {
    .App {
        margin: 0 auto;
        display: grid;
        grid-template-columns: repeat(3, auto);
        grid-template-rows: 300px 1fr 250px;
    }

    .header {
        grid-column: 2/3;
        grid-row: 1/2;
    }

    .imageGrid {
        grid-column: 2/3;
        grid-row: 3/4;
        width: auto;
    }
}
/* end desktop */